import React, { Component } from 'react';
import './style.css';

class Calculator extends Component {
  state = {
    number: "",
    tool: "+",
    total: "0",
  }

  updateNumber = event => {
    const newNumber = "" + this.state.number + event.target.value;
    this.setState({
      ...this.state,
      number: newNumber,
    })
  }

  updateTool = event => {
    this.setState({
      ...this.state,
      tool: event.target.value,
    })

    const newTotal = eval(`${this.state.number}  ${this.state.tool} ${this.state.total}`);
    this.setState({
      ...this.state,
      number: "",
      total: newTotal,
    })
  }

  calculateTotal = () => {
    const total = eval(`${this.state.total} ${this.state.tool} ${this.state.number}`)
    this.setState({
      ...this.state,
      number: total,
    })
  }

  reset = () => {
    this.setState({
      number: "",
      tool: "+",
      total: "0",
    })
  }

  render() {
    return (
      <div className="calculator">
        <div className="wrapper">
          <h3>{this.state.number}</h3>
          <input type="button" value="AC" onClick={ this.reset } />
          <input type="button" value="7" onClick={ this.updateNumber } />
          <input type="button" value="8" onClick={ this.updateNumber } />
          <input type="button" value="9" onClick={ this.updateNumber } />
          <input type="button" value="4" onClick={ this.updateNumber } />
          <input type="button" value="5" onClick={ this.updateNumber } />
          <input type="button" value="6" onClick={ this.updateNumber } />
          <input type="button" value="1" onClick={ this.updateNumber } />
          <input type="button" value="2" onClick={ this.updateNumber } />
          <input type="button" value="3" onClick={ this.updateNumber } />
          <input type="button" value="0" onClick={ this.updateNumber } />
          <input type="button" value="+" onClick={ this.updateTool } className="tool" />
          <input type="button" value="%" onClick={ this.updateTool } className="tool" />
          <input type="button" value="-" onClick={ this.updateTool } className="tool" />
          <input type="button" value="*" onClick={ this.updateTool } className="tool" />
          <input type="button" value="/" onClick={ this.updateTool } className="tool" />
          <input type="button" value="=" onClick={ this.calculateTotal } className="tool" />
        </div>
      </div>
    );
  }
}

export default Calculator;
